@import url(https://fonts.googleapis.com/css2?family=Antonio&family=Spartan&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
body div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
figure,
footer,
hgroup,
menu,
nav,
section,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  box-sizing: border-box;
}

a,
a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
  color: #fff;
  font-family: "Spartan", sans-serif;
  font-weight: bold;
  font-size: 11px;
  line-height: 25px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

h1 {
  font-family: "Antonio", sans-serif;
  font-size: 80px;
  font-weight: normal;
  line-height: 104px;
  text-transform: uppercase;
}

@media screen and (max-width: 750px) {
  h1 {
    font-size: 40px;
    line-height: 52px;
  }
}

h2 {
  font-family: "Antonio", sans-serif;
  font-size: 40px;
  font-weight: normal;
  line-height: 52px;
  letter-spacing: -1.5px;
  text-transform: uppercase;
}

@media screen and (max-width: 1025px) {
  h2 {
    font-size: 24px;
    line-height: 31px;
    letter-spacing: -0.9px;
  }
}

@media screen and (max-width: 1025px) {
  h2 {
    font-size: 20px;
    line-height: 26px;
    letter-spacing: -0.75px;
  }
}

h3 {
  font-family: "Spartan", sans-serif;
  font-size: 12px;
  font-weight: bold;
  line-height: 25px;
  letter-spacing: 2.57143px;
}

h4 {
  font-family: "Spartan", sans-serif;
  font-size: 11px;
  font-weight: bold;
  line-height: 25px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

p {
  font-family: "Spartan", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 25px;
}

.App {
  margin: 0 auto;
  background: #070724;
  max-width: 1440px;
  width: 100%;
  color: #fff;
}

header.Menu {
  height: 85px;
  padding: 22px 40px 27px 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  border-bottom: 1px #393950 solid;
}

@media screen and (max-width: 1025px) {
  header.Menu {
    height: 160px;
    flex-direction: column;
    justify-content: inherit;
    padding: 0;
  }
}

@media screen and (max-width: 750px) {
  header.Menu {
    align-items: initial;
  }

  header.Menu.menu-visible {
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: 1;
    background: #070724;
  }

  header.Menu.menu-hidden {
    height: 68px;
    overflow: hidden;
  }
}

header.Menu h2.title {
  font-size: 28px;
  line-height: 36px;
  letter-spacing: -1.05px;
  text-transform: uppercase;
}

@media screen and (max-width: 1025px) {
  header.Menu h2.title {
    margin: 32px 0 39px 0;
  }
}

@media screen and (max-width: 750px) {
  header.Menu h2.title {
    height: 68px;
    display: flex;
    align-items: center;
    margin: 0 24px;
    justify-content: space-between;
  }

  header .menu-visible {
    height: 598px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 24px;
    padding: 44px 0 67px 45px;
  }

  header .menu-hidden {
    height: 0;
  }
}

header.Menu h2.title .logo-svg {
  display: none;
}

@media screen and (max-width: 750px) {
  header.Menu h2.title .logo-svg {
    display: flex;
    cursor: pointer;
  }
}

header.Menu nav.items a:not(:last-child) {
  margin-right: 33px;
}

header.Menu nav.items a {
  position: relative;
}

header.Menu nav.items a:before {
  position: absolute;
  content: "";
  height: 4px;
  width: 0;
  bottom: 41px;
  overflow: hidden;
  transition: width ease 0.2s;
}

@media screen and (max-width: 1025px) {
  header.Menu nav.items a:before {
    bottom: 0;
    top: 41px;
  }
}

@media screen and (max-width: 750px) {
  header.Menu nav.items a:before {
    content: "";
    width: 20px;
    height: 20px;
    border-radius: 50%;
    bottom: 0;
    top: 0;
    left: -45px;
    transition: none;
  }
}

header.Menu nav.items a:hover::before {
  width: 100%;
}

@media screen and (max-width: 750px) {
  header.Menu nav.items a:hover::before {
    width: 0;
  }
}

header.Menu nav.items a.Mercury:before {
  background: #419ebb;
}

header.Menu nav.items a.Venus:before {
  background: #eda249;
}

header.Menu nav.items a.Earth:before {
  background: #6d2ed5;
}

header.Menu nav.items a.Mars:before {
  background: #d14c32;
}

header.Menu nav.items a.Jupiter:before {
  background: #d83a34;
}

header.Menu nav.items a.Saturn:before {
  background: #cd5120;
}

header.Menu nav.items a.Uranus:before {
  background: #1ec1a2;
}

header.Menu nav.items a.Neptune:before {
  background: #2d68f0;
}

.Tabs .tab-image {
  grid-area: image;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 1025px) {
  .Tabs .tab-image {
    height: 460px;
  }
}

@media screen and (max-width: 750px) {
  .Tabs .tab-image {
    height: 304px;
  }
}

.Tabs .tab-image .img {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 1025px) {
  .Tabs .tab-image .img img {
    width: 28%;
  }
}

@media screen and (max-width: 750px) {
  .Tabs .tab-image .img img {
    width: 250px;
  }
}

.Tabs .tab-image .geo-img {
  position: absolute;
  top: 70%;
  width: 163px;
}

@media screen and (max-width: 1025px) {
  .Tabs .tab-image .img img.geo-img {
    width: 100px;
  }
}

@media screen and (max-width: 750px) {
  .Tabs .tab-image .img img.geo-img {
    width: 75px;
  }
}

.Tabs .tab-content {
  margin: 126px 0 39px 0;
  padding-right: 165px;
  grid-area: content;
  max-width: 516px;
  width: 100%;
}

@media screen and (max-width: 1025px) {
  .Tabs .tab-content {
    max-width: 339px;
    margin: 0 69px 27px 39px;
    padding: 0;
  }
}

@media screen and (max-width: 750px) {
  .Tabs .tab-content {
    margin: 0 auto;
    text-align: center;
  }
}

.Tabs .tab-content h1 {
  margin-bottom: 23px;
}

@media screen and (max-width: 750px) {
  .Tabs .tab-content h1 {
    margin-bottom: 16px;
  }
}

.Tabs .tab-content .container > p {
  margin-bottom: 24px;
}

@media screen and (max-width: 750px) {
  .Tabs .tab-content .container > p {
    margin-bottom: 10px;
  }
}

.Tabs .tab-content .container > div a,
.Tabs .tab-content .container > div > span {
  font-size: 14px;
  line-height: 25px;
  mix-blend-mode: normal;
  opacity: 0.5;
  transition: opacity 0.5s ease;
}

.Tabs .tab-content .container > div a > span {
  font-weight: bold;
  font-size: 14px;
  line-height: 25px;
  margin-right: 8px;
}

.Tabs .tab-content .container > div a {
  text-decoration: underline;
}

.Tabs .tab-content .container > div a:hover {
  opacity: 1;
}

.Tabs .tab-titles {
  grid-area: titles;
  margin-bottom: 87px;
  padding-right: 165px;
  max-width: 516px;
  width: 100%;
}

@media screen and (max-width: 1025px) {
  .Tabs .tab-titles {
    margin: 0 40px 0 70px;
    padding: 0;
    width: 281px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

@media screen and (max-width: 750px) {
  .Tabs .tab-titles {
    flex-direction: row;
    justify-content: space-between;
    margin: 0;
    max-width: 100%;
    width: 100%;
    height: 50px;
  }
}

.Tabs ul.tab-titles li.title:not(:last-child) {
  margin-bottom: 16px;
}

@media screen and (max-width: 750px) {
  .Tabs ul.tab-titles li.title:not(:last-child) {
    margin-bottom: 0;
  }
}

.Tabs ul.tab-titles li.title p > span {
  margin-right: 25px;
  text-transform: uppercase;
}

@media screen and (max-width: 750px) {
  .Tabs ul.tab-titles li.title p,
  .Tabs ul.tab-titles li.title p > span {
    margin: auto;
    font-size: 9px;
    line-height: 10px;
  }
}

@media screen and (max-width: 750px) {
  .Tabs ul.tab-titles li.title p > span:first-child {
    display: none;
  }
}

.Tabs ul.tab-titles li.title p,
.Tabs ul.tab-titles li.title span {
  display: flex;
}

.Tabs ul.tab-titles li.title .subtitle {
  display: block;
}

@media screen and (max-width: 750px) {
  .Tabs ul.tab-titles li.title .subtitle {
    display: none;
  }
}

.Tabs {
  display: grid;
  grid-template-rows: 1fr -webkit-min-content;
  grid-template-rows: 1fr min-content;
  grid-template-columns: 1fr 3fr;
  grid-template-areas:
    "image image content"
    "image image titles";
}

@media screen and (max-width: 1025px) {
  .Tabs {
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    grid-template-areas:
      "image image"
      "content titles";
  }
}

@media screen and (max-width: 750px) {
  .Tabs {
    grid-template-areas:
      "titles"
      "image"
      "content";
  }
}

.Planet ul.extra-information {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 0 165px 56px 165px;
  list-style-type: none;
}

@media screen and (max-width: 1025px) {
  .Planet ul.extra-information {
    padding: 0 40px 36px 40px;
  }
}

@media screen and (max-width: 750px) {
  .Planet ul.extra-information {
    flex-direction: column;
    padding: 28px 24px 47px 24px;
  }
}

.Planet ul.extra-information li {
  max-width: 255px;
  width: 100%;
  height: 128px;
  border: 1px solid #393950;
  padding: 20px 0 27px 23px;
}

@media screen and (max-width: 1025px) {
  .Planet ul.extra-information li {
    max-width: 164px;
    height: 88px;
    padding: 16px 0 19px 15px;
  }
}

@media screen and (max-width: 750px) {
  .Planet ul.extra-information li {
    max-width: 100%;
    height: 48px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 24px;
    margin-bottom: 8px;
  }
}

.Planet ul.extra-information li h4 {
  font-weight: bold;
  font-size: 11px;
  line-height: 25px;
  letter-spacing: 1px;
  text-transform: uppercase;
  opacity: 0.5;
}

